/* -----------------------------------Start Head------------------------------- */

.head {
  padding: 20px 0;
  color: var(--main-white-color);
}
.logo h1 {
  font-size: 35px;
  font-weight: 900;
}

.head .icon {
  margin-left: 10px;
}

/* -----------------------------------End Head------------------------------- */
/* -- */
/* -----------------------------------Start Header------------------------------- */
header {
  background-color: var(--background-color-gray);
  margin: 0 30px;
  position: relative;
}

header ul {
  padding: 30px 20px;
}

header li {
  margin-right: 40px;
}

header ul li a {
  color: var(--main-white-color);
  font-weight: 500;
  transition: 0.5;
}

header ul li a:hover {
  color: var(--main-color);
}

.start {
  background-color: var(--main-color);
  padding: 30px 70px;
  color: var(--main-white-color);
  clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
}

.toggle {
  display: none;
}

@media screen and (max-width: 768px) {
  .start {
    clip-path: none;
  }
  header {
    margin: 0;
    background-color: var(--main-color);
    position: relative;
  }
  header ul.flexSB {
    display: none;
  }

  header ul li {
    margin-bottom: 20px;
  }
  .toggle {
    display: block;
    background: none;
    color: var(--main-white-color);
    font-size: 30px;
    position: absolute;
    right: 50px;
    top: -20px;
  }
  .mobile-nav {
    position: absolute;
    top: 7vh;
    left: 0;
    width: 100%;
    background-color: var(--main-color);
  }
}
/* -----------------------------------End Header------------------------------- */
