@import url("https://fonts.googleapis.com/css2?family=Agdasima&family=Alata&family=Open+Sans:ital,wght@0,300;0,400;1,400&family=Poppins:wght@300;500;700;800&family=Quicksand&family=Roboto:wght@500&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #f8f8f8;
}

.container {
  max-width: 85%;
  margin: auto;
}

.flexSB {
  display: flex;
  justify-content: space-between;
}
.icon {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.3);
  color: var(--main-color);
  transition: 0.5s;
}
.icon:hover {
  cursor: pointer;
  background-color: var(--main-color);
  color: var(--main-white-color);
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  border: none;
}

.row {
  width: 50%;
}

button {
  padding: 17px 30px;
  background-color: var(--main-white-color);
  color: var(--main-color);
  font-weight: 600;
  border: none;
  margin: 30px 10px 0 0;
  cursor: pointer;
  box-shadow: 0 24px 36px -11px rgba(0 0 0 /9%);
}

button i {
  margin-left: 20px;
}

button:hover {
  box-shadow: none;
}

.primary-btn {
  background-color: var(--main-color);
  color: var(--main-white-color);
}

#heading {
  text-align: center;
  padding: 40px 0;
}

#heading h3 {
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--main-color);
  text-transform: uppercase;
}

#heading h1 {
  font-size: 45px;
  margin: 20px 0;
  text-transform: capitalize;
}

p {
  line-height: 30px;
  font-size: 18px;
}

.back {
  background-image: url("../public/images/back.webp");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  padding-top: 30%;
  padding-right: 50px;
  color: var(--main-white-color);
  text-align: center;
}

.back h1 {
  font-size: 100px;
  font-weight: 400;
}

.back h2 {
  font-weight: 500;
  font-size: 17px;
  text-transform: uppercase;
}

.marigin {
  margin-top: 40.3%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
}
.flex {
  display: flex;
}

.grid2 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}

.outline-btn {
  margin: 0;
  box-shadow: none;
  border: 2px solid var(--main-color);
  width: 100%;
  transition: 0.5s;
}

.outline-btn:hover {
  background-color: var(--main-color);
  color: var(--main-white-color);
}

.grid3 {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 10px;
}

.padding {
  padding: 80px 0;
}

.shadow {
  box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
  background-color: var(--main-white-color);
}
@media screen and (max-width: 768) {
  .grid3,
  .grid2,
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .back {
    background-position: center;
    padding-top: 50%;
    height: 80vh;
  }
}
