.formCotainer {
  background-color: var(--main-white-color);
  border-radius: 50px;
  margin: 90px auto 370px;
  height: 500px;
  width: 60%;
  position: relative;
  top: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.myform {
  width: 100%;
  position: absolute;
  text-align: center;
  color: grey;
  font-size: 14px;
}

.myform input {
  width: 60%;
  height: 30px;
  margin: 20px;
}

.login-btn {
  margin: 0;
  box-shadow: none;
  background-color: var(--main-color);
  color: var(--main-white-color);
  width: 20%;
  transition: 0.5s;
  border: 2px solid var(--main-color);
}

.login-btn:hover {
  background-color: var(--main-white-color);
  color: black;
}
